<script>
import {get_item} from "../service/item.js";

const {JX3BOX} = require("@jx3box/jx3box-common");

export default {
  name: "Item",
  props: ['item', 'item_id'],
  data() {
    return {
      source: null,
    };
  },
  watch: {
    'item': {
      immediate: true,
      handler(){
        this.source = this.item;
      }
    },
    'item_id': {
      immediate: true,
      handler() {
        if (this.item_id) {
          get_item(this.item_id).then((res) => {
            let data = res.data;
            if (data.code === 200) {
              let item = data.data.item;
              if (JSON.stringify(item) !== '{}') this.source = item;
            }
          });
        }
      }
    }
  }
};
</script>

